@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
body, button, link, textarea, input {
  font-family: Roboto, sans-serif;
}

button {
  cursor: pointer;
}

.btn {
  border-radius: 74px;
  justify-content: center;
  align-items: center;
  padding: 12px 24px;
  font-family: inherit;
  font-weight: normal;
  display: flex;
}

.link {
  color: currentColor;
  font-style: normal;
  text-decoration: none;
}

img {
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.modal-backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: scale(1.3);
}

:root {
  --accent-color: #f87719;
